.filterLabel {
  font-family: 'Oswald', sans-serif;
  font-size: 1em;
  font-weight: bold;
  margin-bottom: 0.5em;
}
.filterInput {
  width: 65%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
}
.div {
  width: 435px;
  padding-top: 20px;
  padding-left: 30px;
  padding-right: 30px;
  background-color: rgb(201, 239, 84);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
}
